import $ from 'jquery';
import BaseModule from './../Base.module';
import fasterdom from 'fasterdom';

import VideoPlayerBackgroundController from './controller/VideoPlayerBackground.controller';
import VideoPlayerFullscreenController from './controller/VideoPlayerFullscreen.controller';
import VideoPlayerInlineController from './controller/VideoPlayerInline.controller';

/*
 TODO refactor to avoid videojs loading on ios devices
 */

/**
 * Providing {VideoPlayerController} instances necessary for the current page instance.
 * @extends {BaseModule}
 */
class VideoModule extends BaseModule {

	constructor ( config ) {

		super( config );
	}

	init () {
		super.init();

		let controllerInstance = null;

		fasterdom.measureInstant( () => {
			if ( this.node.hasClass( 'videoplayer--bg' ) ) {
				if ( $( 'html' ).hasClass( 'is-mobile' ) ) {
					return;
				}
				controllerInstance = new VideoPlayerBackgroundController();
			}
			
			if ( this.node.hasClass( 'videoplayer--inline' ) ) {
				controllerInstance = new VideoPlayerInlineController();
			}
			
			if ( this.node.hasClass( 'videoplayer--fullscreen' ) ) {
				controllerInstance = new VideoPlayerFullscreenController();
			}

			this.player = controllerInstance.init( this.node );
		} );
	}
}

export default VideoModule;



