import VideoPlayerController from './VideoPlayer.controller';
import $ from 'jquery';

import fasterdom from 'fasterdom';
import device from 'device';

const playerArgs        = {
	      fluid :    true,
	      autoplay : true,
	      loop :     true,
	      muted :    true,
	      controls : false
      },
      // youtube specific settings
      youtubePlayerArgs = {
	      autoplay :       true,
	      controls :       0,
	      disablekb :      1,
	      list :           'user-uploads',
	      modestbranding : 1,
	      rel :            0
      };

/**
 * Add resize handler
 *
 * @return {void}
 */
function addResizeHandler () {
	const self = this;

	if ( self.$scope.closest( '[data-tpl*="sta"]' ).length > 0 ) {
		const posterImg = self.$scope.find( 'img.posterimg' ),
					videoplayerContent = self.$scope.find( '.videoplayer' );

		device.resizeEvent.subscribe( () => {
			let value = -1,
					prop = 'width';

			fasterdom.measure( () => {
				if ( posterImg.outerWidth() === posterImg.offsetParent().outerWidth() ) {
					value = posterImg.outerHeight();
					prop = 'height';
				} else if ( posterImg.outerHeight() === posterImg.offsetParent().outerHeight() ) {
					value = posterImg.outerWidth();
					prop = 'width';
				}
			} );
			fasterdom.mutate( () => {
				if ( prop === 'width' ) {
					videoplayerContent.width( value );
					videoplayerContent.height( '' );
				} else if ( prop === 'height' ) {
					videoplayerContent.width( '' );
					videoplayerContent.height( value );
				}
			} );
		} );
	}
}

/**
 * Class representing a background video instance.
 * Controls the state of the navigation.
 * Controls menu item behavior.
 * @extends { VideoPlayerController }
 */
class VideoPlayerBackgroundController extends VideoPlayerController {

	constructor () {
		super();

		this.args              = $.extend( true, {}, playerArgs );
		this.youtubePlayerArgs = $.extend( true, {}, youtubePlayerArgs );
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @param { object } $root - jQuery wrapped DOM node
	 * @public
	 * @returns {VideoPlayerController} - instance
	 */
	init ( $root ) {
		const self = this;

		super.init( $root );

		self.player.ready( () => {
			if ( self.player.tech_.ytPlayer ) {
				self.player.tech_.ytPlayer.mute( true );

				addResizeHandler.call( this );
			}
			self.player.muted( true );
		} );
		
		return this;
	}
}

export default VideoPlayerBackgroundController;



