import AbstractController from './../../../base/controller/Abstract.controller';
import bucket from './../../../services/Bucket.service';

import $ from 'jquery';
import videojs from 'sensory-video.js';
import 'sensory-video.js-youtube';
import fasterdom from 'fasterdom';

const useragent         = window.UserAgent,
      playerArgs        = {
	      fluid :      true,
	      autoplay :   false,
	      controlBar : {
		      volumeMenuButton : {
			      inline :   false,
			      vertical : true
		      }
	      }
      },
      // youtube specific settings
      youtubePlayerArgs = {
	      autoplay :       false,
	      controls :       1,
	      disablekb :      1,
	      list :           'user-uploads',
	      modestbranding : 1,
	      rel :            0
      };

/**
 * Attach event handlers to player events
 * @returns {void}
 */
function attachEvents () {
	fasterdom.mutateInstant( () => {
		this.$scope.addClass( 'videojs-loading' );
	} );

	fasterdom.measureInstant( () => {
		const poster = this.$scope.find( '.videoplayer' ).attr( 'poster' );

		if ( poster ) {
			const tmpImg = new Image();

			tmpImg.onload = () => {
				fasterdom.mutateInstant( () => {
					this.$scope.addClass( 'poster-loaded' );
				} );
			};
			tmpImg.src    = poster;
		}
	} );

	this.player.ready( () => {
		fasterdom.mutateInstant( () => {
			this.$scope.addClass( 'videojs-ready' );
			// youtube sends no loaded event, only ready
			if ( this.isYouTube ) {
				this.$scope.removeClass( 'videojs-loading' );
				this.$scope.addClass( 'videojs-loaded' );
			}
		} );
	} );

	this.player.on( 'loadedmetadata', () => {
		fasterdom.mutateInstant( () => {
			this.$scope.removeClass( 'videojs-loading' );
			this.$scope.addClass( 'videojs-loaded' );
		} );
	} );

	this.player.on( 'play', () => {
		this.isPlaying = true;
		fasterdom.mutateInstant( () => {
			this.$scope.removeClass( 'videojs-is-ended videojs-is-paused' );
			this.$scope.addClass( 'videojs-is-playing' );
		} );
	} );

	this.player.on( 'pause', () => {
		this.isPlaying = false;
		fasterdom.mutateInstant( () => {
			this.$scope.addClass( 'videojs-is-paused' );
		} );
	} );

	this.player.on( 'ended', () => {
		this.isPlaying = false;
		fasterdom.mutateInstant( () => {
			this.$scope.addClass( 'videojs-is-ended' );
			this.$scope.removeClass( 'videojs-is-playing' );

			this.player.hasStarted( false );
			this.player.load();
		} );
	} );
}

/**
 * Class representing a video instance.
 * Controls the state of the navigation.
 * Controls menu item behavior.
 * @extends { AbstractController }
 */
class VideoPlayerController extends AbstractController {

	constructor () {
		super();

		this.$scope = null;

		this.url = null;

		this.player        = null;
		this.playerElement = null;

		this._isYouTube = false;
		this._isPlaying = false;

		this.args              = $.extend( true, {}, playerArgs );
		this.youtubePlayerArgs = $.extend( true, {}, youtubePlayerArgs );
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @param { object } $root - jQuery wrapped DOM node
	 * @public
	 * @returns {VideoPlayerController} - instance
	 */
	init ( $root ) {
		const self = this;

		this.$scope        = $root;
		this.playerElement = self.$scope.find( 'video' );
		// TODO: move to fasterdom?
		//fasterdom.measureInstant( () => {
		this.url = self.playerElement.attr( 'data-url' );

		if ( self.url.match( /youtu\.?be/g ) !== null ) {
			this.isYouTube = true;
			fasterdom.mutateInstant( () => {
				self.$scope.addClass( 'youtube' );
			} );
			$.extend( self.args, self.youtubePlayerArgs, {
				techOrder : [ 'youtube', 'html5' ],
				sources :   [
					{
						type : 'video/youtube',
						src :  self.url
					}
				]
			} );
		} else {
			const videoExtension = self.url.split( '.' ).pop(),
			      tracks         = self.playerElement.attr( 'data-tracks' );

			//fasterdom.mutateInstant( () => {
			if ( videoExtension === 'mp4' ) {
				self.playerElement.append( '<source src="' + self.url + '" type="video/mp4">' );
			} else if ( videoExtension === 'webm' ) {
				self.playerElement.append( '<source src="' + self.url + '" type="video/webm">' );
			} else if ( videoExtension === 'ogg' ) {
				self.playerElement.append( '<source src="' + self.url + '" type="video/ogg">' );
			} else {
				self.playerElement.append( '<p>Not a valid source file</p>' );
			}

			if ( tracks ) {
				const prasedTracks = JSON.parse( tracks );

				$.each( prasedTracks, ( i, v ) => {
					self.playerElement.append( '<track kind="subtitles" label="' + v.label + '" src="' + v.src + '" srclang="' + v.lang + '" ' + ( i === 0 ?
					                                                                                                                               'default' :
					                                                                                                                               '' ) + '></track>' );
				} );
			}
			//} );
		}

		const aspectRatio = self.determineAspectRatio(); // eslint-disable-line one-var

		fasterdom.mutateInstant( () => {
			self.$scope.addClass( 'ar-' + aspectRatio );
		} );

		//} );

		self.player = videojs( self.playerElement[ 0 ], self.args );

		attachEvents.call( self );

		return this;
	}

	determineAspectRatio () {
		if ( !this.$scope.find( '.posterimg' ).length ) {
			return false;
		}
		// TODO: function call is in fasterdom.measure, but shouldn't it also be moved there?
		const width       = this.$scope.find( '.posterimg' ).outerWidth(),
		      height      = this.$scope.find( '.posterimg' ).outerHeight(),
		      aspectRatio = width / height,
		      resolutions = {
			      '21-9' :  21 / 9,
			      '16-9' :  16 / 9,
			      '16-10' : 16 / 10,
			      '4-3' :   4 / 3
		      };

		let closest = '16-9',
		    current = resolutions[ closest ],
		    diff    = Math.abs( aspectRatio - current );

		$.each( resolutions, ( i, v ) => {
			const newdiff = Math.abs( aspectRatio - v );

			if ( newdiff < diff ) {
				diff    = newdiff;
				closest = i;
				current = v;
			}
		} );

		return closest;
	}

	set isYouTube ( is ) {
		this._isYouTube = is === true;
	}

	get isYouTube () {
		return this._isYouTube;
	}

	set isPlaying ( is ) {
		this._isPlaying = is === true;
	}

	get isPlaying () {
		return this._isPlaying;
	}

	playFullscreen () {
		this.player.play();
		this.player.requestFullscreen();
	}

	setFullscreenHandler ( pauseOnExit = true ) {

		if ( useragent.isMobile && !useragent.isAndroid && !useragent.isAndroidTablet ) {
			this.player.on( 'play', () => {
				this.player.requestFullscreen();
			} );
		} else {
			this.player.ready( () => {
				const bigPlayButton = this.player.getChild( 'bigPlayButton' );

				bigPlayButton.off( 'click' );
				bigPlayButton.off( 'tap' );
				bigPlayButton.on( 'click', this.playFullscreen.bind( this ) );
				bigPlayButton.on( 'tap', this.playFullscreen.bind( this ) );
			} );
		}

		this.player.on( 'ended', () => {
			this.player.exitFullscreen();
		} );

		this.player.on( 'fullscreenchange', () => {
			if ( pauseOnExit && !this.player.isFullscreen() ) {
				this.player.pause();
			}
		} );
	}

}

export default VideoPlayerController;



