import VideoPlayerController from './VideoPlayer.controller';
import bucket from './../../../services/Bucket.service';

const useragent = window.UserAgent;

/**
 * Class representing a inline video instance.
 * Controls the state of the navigation.
 * Controls menu item behavior.
 * @extends { VideoPlayerController }
 */
class VideoPlayerInlineController extends VideoPlayerController {

	constructor () {
		super();
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @param { object } $root - jQuery wrapped DOM node
	 * @public
	 * @returns {VideoPlayerController} - instance
	 */
	init ( $root ) {
		const self = this;

		super.init( $root );

		if ( useragent.isMobile ) {
			self.setFullscreenHandler();
		}

		return this;
	}
}

export default VideoPlayerInlineController;



